import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react';
import { Fade, Badge, Card, CardBody, CardHeader, Col, Dropdown, DropdownItem, DropdownMenu, DropdownToggle, Row, Table, Modal, ModalHeader, ModalBody } from 'reactstrap';
import { stockLiveApi } from '../../api/live';
import { MarketContext } from '../../context/contexts';
import Spinner from '../../components/spinner/Spinner';

function StockLive(props) {

    const { market } = useContext(MarketContext);
    const [heading, setHeading] = useState([]);
    const [fullTransaction, setFullTransaction] = useState([]);
    const [transactions, setTransactions] = useState([]);
    const [index, setIndex] = useState([]);
    const [volume, setVolume] = useState('0');
    const [turnover, setTurnover] = useState('0');
    const [buttonDropdown, setButtonDropdown] = useState(false);
    const [filterType, setFilterType] = useState('Latest');
    const [sortDirection, setSortDirection] = useState('random');
    const [showMarketDepth, setShowMarketDepth] = useState(false);
    const [companySymbol, setCompanySymbol] = useState();
    const [depth, setDepth] = useState({});
    const [loading, setLoading] = useState(false);
    const [fadeIn, setFadein] = useState(true);


    useEffect(() => {
        if (market == "Live" && showMarketDepth) {
            let toggleId = -1;
            toggleId = setInterval(() => {
                setFadein(!fadeIn)
            }, 600);
            return () => {
                clearInterval(toggleId);
            }
        } else {
            setFadein(true);
        }
    });




    const updateTransaction = () => {
        axios(stockLiveApi)
            .then(liveData => {
                setHeading(liveData.data.tableHead);
                setFullTransaction(liveData.data.transactions);
                setIndex(liveData.data.index);
                setVolume(liveData.data.volume);
                setTurnover(liveData.data.turnover);
            })
    }

    const SortDirectionIcon = () => {
        if (sortDirection == 'ascending') {
            return <i onClick={() => setSortDirection('descending')} className="fa fa-arrow-up" />
        } else if (sortDirection == "descending") {
            return <i onClick={() => setSortDirection('random')} className="fa fa-arrow-down" />
        } else {
            return <i onClick={() => setSortDirection('ascending')} className="fa fa-arrows-h" />
        }
    }


    useEffect(() => {
        updateTransaction();
        if (market == 'Live') {
            const intervalId = setInterval(updateTransaction, 60000);
            return () => {
                clearInterval(intervalId);
            }
        }
    }, [market]);

    useEffect(() => {
        let newTransactions;
        if (filterType == "Gainers") {
            newTransactions = fullTransaction.filter(transaction => parseFloat(transaction[4]) > 0);
        } else if (filterType == "Losers") {
            newTransactions = fullTransaction.filter(transaction => parseFloat(transaction[4]) < 0);
        } else if (filterType == "Stable") {
            newTransactions = fullTransaction.filter(transaction => parseFloat(transaction[4]) == 0);
        } else {
            newTransactions = fullTransaction.slice();
        }

        if (sortDirection == "ascending") {
            if (filterType == "Latest" || filterType == "Stable") {
                newTransactions.sort((a, b) => {
                    if (a[1] === b[1]) {
                        return 0;
                    }
                    else {
                        return (a[1] < b[1]) ? -1 : 1;
                    }
                })
            } else {
                newTransactions.sort((t1, t2) => parseFloat(t2[5]) - parseFloat(t1[5]));
            }
        }
        if (sortDirection == "descending") {
            if (filterType == "Latest" || filterType == "Stable") {
                newTransactions.sort((b, a) => {
                    if (a[1] === b[1]) {
                        return 0;
                    }
                    else {
                        return (a[1] < b[1]) ? -1 : 1;
                    }
                })
            } else {
                newTransactions.sort((t1, t2) => parseFloat(t1[5]) - parseFloat(t2[5]));
            }

        }
        setTransactions(newTransactions);
    }, [filterType, sortDirection, fullTransaction]);

    const ColoredDropDownToggle = (props) => {
        if (filterType == "Gainers") {
            return (<DropdownToggle caret color="success">
                {props.children}
            </DropdownToggle>)
        } else if (filterType == "Losers") {
            return (<DropdownToggle caret color="danger">
                {props.children}
            </DropdownToggle>)
        } else if (filterType == "Stable") {
            return (<DropdownToggle caret color="primary">
                {props.children}
            </DropdownToggle>)
        } else {
            return (<DropdownToggle caret color="warning">
                {props.children}
            </DropdownToggle>)
        }
    }

    const openMarketDepthModal = (symbol => {
        setShowMarketDepth(true);
        setLoading(true);
        setDepth(false);
        setCompanySymbol(symbol);
        axios.get('https://api.nepsechart.com/requestHandler/symbol.php?request=MARKETDEPTH&symbol=' + symbol)
            .then(function (response) {
                setLoading(false);
                setDepth(response.data);
            })
            .catch(function (error) {

            })
    })

    useEffect(() => {
        if (companySymbol) {
            const intervalId = setInterval(() => {
                axios.get('https://api.nepsechart.com/requestHandler/symbol.php?request=MARKETDEPTH&symbol=' + companySymbol)
                    .then(function (response) {
                        setLoading(false);
                        setDepth(response.data);
                    })
                    .catch(function (error) {

                    })
            }, 30000);

            return () => {
                clearInterval(intervalId)
            }
        }
    }, [companySymbol]);

    const tableHead = heading.map(head => {
        if (head == '%Change') {
            return <th key={head}>{head} <SortDirectionIcon /></th>
        } else if (['S.N.', 'Open', 'LTV', 'High', 'Low', 'Volume', 'Previous Closing'].includes(head)) {
            return <th className="d-none d-sm-table-cell" key={head}>{head}</th>
        }
        else {
            return <th key={head}>{head}</th>
        }

    });



    const tableBody = transactions.map(transaction => {
        let rowBackground;
        if (parseFloat(transaction[4]) > 0) {
            rowBackground = "table-success";
        } else if (parseFloat(transaction[4]) < 0) {
            rowBackground = "table-danger"
        } else {
            rowBackground = "table-primary"
        }
        return (
            <tr key={transaction[1]} className={rowBackground} onClick={() => openMarketDepthModal(transaction[1])}>
                {transaction.map((data, index) => {
                    if (index > 0 && index < 6 && index != 3) {
                        return <td key={index}>{data}</td>
                    } else {
                        return <td className="d-none d-sm-table-cell text-small" key={index}>{data}</td>
                    }

                }
                )}
            </tr>)
    });



    return (
        <React.Fragment>
            <Row>
                <Col className="px-0">
                    <Card>
                        <CardHeader className="bg-secondary">
                            <Row>
                                <Col sm="2">
                                    NEPSE | <strong className={parseFloat(index[2]) >= 0 ? "text-success" : "text-danger"}>{index[1]}</strong>
                                </Col>
                                <Col sm="2">
                                    Change | <strong className={parseFloat(index[2]) >= 0 ? "text-success" : "text-danger"}> {index[2]} <small>({index[3]}) </small></strong>

                                </Col>
                                <Col sm="2">
                                    Volume | {volume}
                                </Col>
                                <Col sm="3">
                                    Turnover | {turnover}
                                </Col>
                                <Col sm="3">
                                    <Dropdown className="float-right" size="sm" isOpen={buttonDropdown} toggle={() => { setButtonDropdown(!buttonDropdown) }}>
                                        <ColoredDropDownToggle>
                                            {filterType}
                                        </ColoredDropDownToggle>
                                        <DropdownMenu size="sm" right>
                                            <DropdownItem className="bg-warning" onClick={e => setFilterType(e.target.innerText)} >Latest</DropdownItem>
                                            <DropdownItem className="bg-success" onClick={e => setFilterType(e.target.innerText)} >Gainers</DropdownItem>
                                            <DropdownItem className="bg-danger" onClick={e => setFilterType(e.target.innerText)} >Losers</DropdownItem>
                                            <DropdownItem className="bg-primary" onClick={e => setFilterType(e.target.innerText)} >Stable</DropdownItem>
                                        </DropdownMenu>
                                    </Dropdown>
                                </Col>
                            </Row>
                        </CardHeader>
                        <CardBody>
                            <Row>
                                <Col className="px-0 mx-0">
                                    <Table responsive bordered size="sm">
                                        <thead className="bg-primary">
                                            <tr>
                                                {tableHead}
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {tableBody}
                                        </tbody>
                                    </Table>
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Col>
            </Row>

            <Modal size="lg" isOpen={showMarketDepth} toggle={() => setShowMarketDepth(!showMarketDepth)}>
                <ModalHeader toggle={() => setShowMarketDepth(!showMarketDepth)}>
                    <Row>
                        <strong className="pl-2">{companySymbol} Market Depth </strong>
                        <Fade in={fadeIn} className="pl-2"><Badge color="primary"><small>{market}</small></Badge></Fade>

                    </Row>
                </ModalHeader>
                <ModalBody>
                    {depth ?
                        <Card>
                            {depth.hasOwnProperty('pohlc') ?
                                <CardHeader>
                                    {loading ? null
                                        :
                                        <Row>
                                            <Col sm="4">
                                                <strong>{depth.pohlc[0]} </strong>
                                                <small className="text-muted"> Previous Close</small>
                                            </Col>
                                            <Col sm="2">
                                                <strong>{depth.pohlc[1]}</strong>
                                                <small className="text-muted"> Open</small>
                                            </Col>
                                            <Col sm="2">
                                                <strong>{depth.pohlc[2]}</strong>
                                                <small className="text-muted"> High</small>
                                            </Col>
                                            <Col sm="2">
                                                <strong>{depth.pohlc[3]}</strong>
                                                <small className="text-muted"> Low</small>
                                            </Col>
                                            <Col sm="2">
                                                <strong>{depth.pohlc[4]}</strong>
                                                <small className="text-muted"> Close</small>
                                            </Col>
                                        </Row>
                                    }
                                </CardHeader>
                                :
                                null}

                            <CardBody>
                                {market == 'Live' ?
                                    <Row>
                                        <Col xs="12">
                                            {depth.hasOwnProperty('buyOrder') ?
                                                <Table striped bordered responsive size="sm">
                                                    <thead>
                                                        <tr>
                                                            {depth.buyOrder.header.map(header => <th key={header}>{header}</th>)}
                                                            {depth.sellOrder.header.map(header => <th key={header}>{header}</th>)}
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {depth.buyOrder.data.map((row, index) =>

                                                            <tr key={index}>
                                                                <td>{row[0]}</td>
                                                                <td>{row[1]}</td>
                                                                <td>{row[2]}</td>
                                                                <td>{depth.sellOrder.data[index][0]}</td>
                                                                <td>{depth.sellOrder.data[index][1]}</td>
                                                                <td>{depth.sellOrder.data[index][2]}</td>
                                                            </tr>
                                                        )}
                                                        <tr key="totalBuy">
                                                            <td><strong>Hidden {depth.totalBuy - depth.buyOrder.data.reduce((acc, amount) => amount[1] != '---' ? (acc + amount[1]) : acc, 0)}</strong></td>
                                                            <td><strong>Visible {depth.buyOrder.data.reduce((acc, amount) => amount[1] != '---' ? (acc + amount[1]) : acc, 0)}</strong></td>
                                                            <td><strong>Total {depth.totalBuy}</strong></td>
                                                            <td><strong>Total {depth.totalSell}</strong></td>
                                                            <td><strong>Visible {depth.sellOrder.data.reduce((acc, amount) => amount[1] != '---' ? (acc + amount[1]) : acc, 0)}</strong></td>
                                                            <td><strong>Hidden {depth.totalSell - depth.sellOrder.data.reduce((acc, amount) => amount[1] != '---' ? (acc + amount[1]) : acc, 0)}</strong></td>

                                                        </tr>
                                                    </tbody>
                                                </Table>
                                                :
                                                null}

                                        </Col>
                                    </Row>
                                    :
                                    null
                                }
                            </CardBody>
                        </Card>
                        :
                        <div className="text-center">
                            <strong className="text-center">Loading Market Depth...</strong>
                            <Spinner />
                        </div>
                    }
                </ModalBody>
            </Modal>
        </React.Fragment>
    )
}

export default StockLive;